<template>
    <transition-group 
        id="navigation"
        tag="div"
        name="staggered"
        :css="false"
        :class="$mq"
        v-on:before-enter="beforeEnter"
        v-on:enter="onEnter"
    >
        <router-link
            class="nav-item"
            v-if="initialized"
            v-for="(item, ix) in links"
            v-bind:data-ix="ix"
            :key="item.url"
            :to="item.url"
        >
            {{ item.title }}
        </router-link>
    </transition-group>
</template>

<script type="text/ecmascript-6">
import Velocity from 'velocity-animate';

export default {
    name: 'Navigation',
    data () {
        return {
            initialized: false,
            links: [
                { title: 'Home', url: '/' },
                { title: 'Skills', url: '/skills' },
                { title: 'Contact', url: '/contact' },
                { title: 'Blog', url: '/blog' }
            ]
        };
    },
    mounted: function () {
        this.initialized = true;
    },
    methods: {
        beforeEnter: function (el) {
            el.style.opacity = 0;
            el.style.height = 0;
        },
        onEnter: function (el, done) {
            let delay = el.dataset.ix * 250;
            setTimeout(() => Velocity(el, { opacity: 1, height: '1em' }, { complete: done }), delay);
        }
    }
};
</script>

<style scoped>
    #navigation {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: baseline;
        width: 100%;
        transition: all 0.5s ease-out;
        min-height: 1em;
    }

    #navigation.sm {
        margin: 12px 0;
    }
    #navigation.md {
        margin: 24px 0;
    }
    #navigation.lg {
        margin: 32px 0;
    }

    .nav-item {
        color: #334433;
        transition: all 0.5s ease-out;
        outline: none;
        display: inline-block;
    }

    .nav-item.router-link-exact-active {
        color: #266886;
    }

    .nav-item:focus:not(.router-link-exact-active) {
        color: #ababab;
    }

    .nav-item.router-link-exact-active:focus {
        color: #3b845f;
    }

    #navigation .nav-item:not(:last-child) {
        border-right: 1px solid rgba(0, 0, 0, 0.25);
    }

    #navigation.sm .nav-item {
        padding: 0 8px;
    }

    #navigation.md .nav-item {
        padding: 0 12px;
    }

    #navigation.lg .nav-item {
        padding: 0 24px;
    }
</style>



