import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/components/Home';
import Contact from '@/components/Contact';
import Skills from '@/components/Skills';
import Blog from '@/components/Blog';

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Home',
            component: Home
        },
        {
            path: '/skills',
            name: 'Skills',
            component: Skills
        },
        {
            path: '/contact',
            name: 'Contact',
            component: Contact
        },
        {
            path: '/blog',
            name: 'Blog',
            component: Blog
        }
    ]
});



