<template>
    <transition-group
        tag="div"
        name="staggered"
        :css="false"
        v-on:before-enter="beforeEnter"
        v-on:enter="onEnter"
    >
        <div key="exp" v-bind:data-ix="0" class="skill-group" v-show="initialized">
            <h2 class="skill-header">Professional Experience</h2>
            <div class="skill-subgroup-wrapper">
                <div class="skill-subgroup">
                    <h4 class="skill-subheader"><a href="https://www.raytheon.com" target="_blank">Raytheon</a> (2016 - Present)</h4>
                    <p>
                        Developed a weather data processing and monitoring application in Java with a web-based front-end written in 
                        AngularJS
                    </p>
                    <ul>
                        <li>Authored a complex web application using websockets and AngularJS</li>
                        <li>Developed agents that communicated and coordinated over Jetty websockets</li>
                        <li>Developed a number of RESTful services and Jetty servlets (using Spring &amp; Camel)</li>
                        <li>Secured HTTPS endpoints using TLS</li>
                        <li>Developed a number of tools and test harnesses to support the team's development and testing</li>
                        <li>Developed a gulp-based build toolchain that included live-reloading for a tight development loop</li>
