                        <a href="https://www.northropgrumman.com" target="_blank">Northrop Grumman</a> (2013 - 2016)
                    </h4>
                    <p>Developed a full-stack Enterprise-level SOA system using Java, Swing, and JavaFX</p>
                    <ul>
                        <li>Developed a variety of Web Services (REST and SOAP)</li>
                        <li>Developed JMS components (ActiveMQ)</li>
                        <li>Designed and developed rich-client GUIs with JavaFX and Swing</li>
                        <li>Designed and developed data models using XML schemas, generating code with JAXB</li>
                        <li>Worked closely with mapping data and software (WorldWind, GEOJSON, GRIB2, etc.)</li>
                        <li>Worked with MySQL and Postgres databases, using Hibernate as an abstraction layer</li>
                        <li>Developed full-coverage unit tests with JUnit</li>
                        <li>Administered a Linux server running Glassfish for application deployment</li>
                        <li>Ported C# to Java</li>
                        <li>Agile Development methodologies were used</li>
                    </ul>
                </div>
            </div>
        </div>
        <div key="tech" v-bind:data-ix="1" class="skill-group" v-show="initialized">
            <h2 class="skill-header">Technical Skills</h2>
            <div class="skill-subgroup-wrapper">
                <div class="skill-subgroup fixed">
                    <h4>Programming Languages</h4>
                    <ul>
                        <li>Java</li>
                        <li>Javascript (web and Node.js)</li>
                        <li>C/C++</li>
                        <li>C#</li>
                        <li>Python</li>
                        <li>Shell scripting (bash/zsh)</li>
                        <li>Rust</li>
                        <li>Go</li>
                    </ul>
                </div>
                <div class="skill-subgroup fixed">
                    <h4>Web Technologies</h4>
                    <ul>
                        <li>AngularJS (1.x)</li>
                        <li>Vue.js</li>
                        <li>React</li>
                        <li>Node.js</li>
                        <li>jQuery</li>
                        <li>Build toolchains (webpack/gulp/grunt/babel)</li>
                    </ul>
                </div>
                <div class="skill-subgroup fixed">
                    <h4>Operating Systems</h4>
                    <ul>
                        <li>Linux (RHEL/Debian/Ubuntu)</li>
                        <li>Windows 10/8/7</li>
                        <li>Mac OSX</li>
                        <li>Android</li>
                        <li>iOS</li>
                    </ul>
                </div>
                <div class="skill-subgroup fixed">
                    <h4>Java Technologies</h4>
                    <ul>
                        <li>JavaEE</li>
                        <li>JMS</li>
                        <li>Spring</li>
                        <li>Camel</li>
                        <li>JAXB</li>
                        <li>Swing/JavaFX</li>
                        <li>Maven</li>
                        <li>OSGi containers (Felix)</li>
                        <li>Glassfish administration</li>
                    </ul>
                </div>
                <div class="skill-subgroup fixed">
                    <h4>Other Skills &amp; Interests</h4>
                    <ul>
                        <li>Software Security &amp; Cryptography</li>
                        <li>Embedded and real-time systems</li>
                        <li>Wireless sensor &amp; mesh networks</li>
                        <li>Digital Signals Processing in audio</li>
                        <li>Software disassembly and reverse engineering</li>
                    </ul>
                </div>
            </div>
        </div>
        <div key="ed" v-bind:data-ix="2" class="skill-group" v-show="initialized">
            <h2 class="skill-header">Education</h2>
            <div class="skill-subgroup-wrapper">
                <div class="skill-subgroup">
                    <h4 class="skill-subheader">
                        B.S. Computer Science - <a href="https://www.csusm.edu">California State University San Marcos</a>
                    </h4>
                    <p>Graduated 2015</p>
                </div>
            </div>
        </div>
        <div key="hobby" v-bind:data-ix="3" class="skill-group" v-show="initialized">
            <h2 class="skill-header">Hobbies</h2>
            <p>
                I really enjoy programming in Rust. I have a few projects up on my 
                <a href="https://github.com/pulse0ne" target="_blank" >github</a> and 
                <a href="https://gitlab.com/users/tsnedigar/projects" target="_blank">gitlab</a> pages that I have a lot of fun working on. 
                I also have a deep interest in audio DSP. I wrote and maintain a Firefox browser plugin that allows you to equalize any 
                audio playing on an HTML5 audio or video element. This allows users that enjoy music on YouTube to crank the bass, or those 
                who have difficulty hearing to tune the audio to make spoken voices more legible.
            </p>
            <p>
                Outside of working on code, I love spending time with my wonderful wife and daughter. I also love playing and watching ice 
                hockey (go Kings!)
            </p>
        </div>
    </transition-group>
</template>

<script type="text/ecmascript-6">
import Velocity from 'velocity-animate';

export default {
    name: 'Skills',
    data () {
        return { initialized: false };
    },
    mounted: function () {
        this.initialized = true;
    },
    methods: {
        beforeEnter: function (el) {
            el.style.opacity = 0;
        },
        onEnter: function (el, done) {
            let delay = parseInt(el.dataset.ix, 10) * 250 + 250;
            setTimeout(() => Velocity(el, { opacity: 1 }, { complete: done }), delay);
        }
    }
};
</script>

<style scoped>
    .skill-subgroup-wrapper {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }

    .skill-group:not(:last-child) {
        border-bottom: 1px solid rgba(0, 0, 0, 0.25);
    }

    .skill-subgroup {
        margin: 8px;
        max-width: 600px;
    }

    .skill-subgroup.fixed {
        width: 250px;
    }

    .skill-header {
        margin: 6px 0 4px;
    }

    .skill-subheader {
        margin: 0;
    }

    a {
        color: #266886;
    }

    p {
        margin: 8px;
    }

    ul {
        list-style-type: circle;
    }
</style>



