<template>
    <transition-group
        class="content"
        tag="div"
        name="staggered"
        :css="false"
        v-on:before-enter="beforeEnter"
        v-on:enter="onEnter"
    >
        <div class="ts-img-container" v-show="initialized" key="image" :data-ix="0">
            <img src="@/assets/ts.jpg">
        </div>
        <p key="p1" :data-ix="2" v-show="initialized">
            Hello there! My name is Tyler Snedigar, and I'm a Nebraska-based full-stack software developer.
            If I'm not working on some sort of code, I'm probably out playing ice hockey!
        </p>
        <p key="p2" :data-ix="3" v-show="initialized">
            I have a wide-ranging skillset, from HTML5/CSS3/Javascript-based web design and development, to Java enterprise
            services, all the way down to audio signal processing in Rust or C/C++.
        </p>
        <p key="p3" :data-ix="4" v-show="initialized">
            Want front end work? I've got you covered. Need
            service middleware? I'm there. Database integration? Done.
        </p>
        <p key="p4" :data-ix="5" v-show="initialized">
            Send me an email and we'll build something great together!
        </p>
    </transition-group>
</template>

<script type="text/ecmascript-6">
import Velocity from 'velocity-animate';

export default {
    name: 'Home',
    data () {
        return { initialized: false };
    },
    mounted: function () {
        this.initialized = true;
    },
    methods: {
        beforeEnter: function (el) {
            el.style.opacity = 0;
        },
        onEnter: function (el, done) {
            let delay = parseInt(el.dataset.ix, 10) * 250 + 250;
            setTimeout(() => Velocity(el, { opacity: 1 }, { complete: done }), delay);
        }
    }
};
</script>

<style scoped>
    .content {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .ts-img-container {
        position: relative;
        border-radius: 50%;
        min-width: 125px;
        min-height: 125px;
        max-width: 400px;
        max-height: 400px;
        width: 30%;
        overflow: hidden;
        transition: all 0.25s ease-out;
        margin: 12px;
    }

    .ts-img-container > img {
        display: block;
        width: 100%;
        transition: all 0.25s ease-in-out;
    }

    .ts-img-container:hover > img {
        transform: scale(1.05);
    }

    .ts-img-container:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        box-shadow: inset 0 0 4px 1px rgba(56, 65, 56, 1.0);
        border-radius: 50%;
    }

    p {
        font-size: 1.2em;
        text-align: center;
    }
</style>



