<template>
    <div id="app">
    <div id="particles"></div>
        <div id="name-header" :class="$mq">
            Tyler Snedigar
        </div>
        <Navigation />
        <transition name="fade" :css="false" v-on:leave="onLeave" mode="out-in">
            <router-view />
        </transition>
    </div>
</template>

<script>
import Velocity from 'velocity-animate';
import Navigation from '@/components/Navigation';
import 'particles.js';

export default {
    name: 'App',
    components: {
        Navigation
    },
    mounted: function () {
        // eslint-disable-next-line
        particlesJS.load('particles', '/static/particles-config.json');
    },
    methods: {
        onLeave: function (el, done) {
            Velocity(el, { opacity: 0 }, { complete: done, duration: 250 });
        }
    }
};
</script>

<style>
    @import url('https://fonts.googleapis.com/css?family=Montserrat:200|Source+Sans+Pro');

    html {
        padding: 0;
        background-color: #f5f5f7;
        color: #242424;
    }
    body {
        padding: 0;
        margin: 0;
    }
    #app {
        font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        font-size: 14px;
        padding: 16px;
        border-top: 1px solid #3b845f;
    }

    #particles {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 90%;
        z-index: -999;
    }

    #name-header {
        font-family: 'Montserrat', 'Helvetica Neue', sans-serif;
        font-weight: 200;
        text-align: center;
        transition: all 0.5s ease-out;
    }

    #name-header.sm {
        font-size: 2em;
        margin: 12px;
    }

    #name-header.md {
        font-size: 3em;
        margin: 16px;
    }

    #name-header.lg {
        font-size: 4em;
        margin: 20px;
    }

    .fade-leave-active {
        opacity: 0;
        height: 0;
    }
</style>



