<template>
    <transition-group tag="div" name="stag" :css="false" v-on:before-enter="beforeEnter" v-on:enter="onEnter">
        <div key="email" v-show="initialized" v-bind:data-ix="0">
            <h3>Email</h3>
            <a href="mailto:snedigart@gmail.com">snedigart@gmail.com</a>
        </div>
        <div key="phone" v-show="initialized" v-bind:data-ix="1">
            <h3>Phone</h3>
            <a href="tel:402-965-1724">(402) 965-1724</a>
        </div>
    </transition-group>
</template>

<script type="text/ecmascript-6">
import Velocity from 'velocity-animate';

export default {
    name: 'Contact',
    data () {
        return { initialized: false };
    },
    mounted: function () {
        this.initialized = true;
    },
    methods: {
        beforeEnter: function (el) {
            el.style.opacity = 0;
        },
        onEnter: function (el, done) {
            let delay = parseInt(el.dataset.ix, 10) * 250 + 250;
            setTimeout(() => Velocity(el, { opacity: 1 }, { complete: done }), delay);
        }
    }
};
</script>

<style scoped>
* {
    text-align: center;
}

a {
    color: #266886;
}
</style>



