import Vue from 'vue';
import VueMq from 'vue-mq';
import App from './App';
import router from './router';

Vue.config.productionTip = false;

Vue.use(VueMq, {
    breakpoints: {
        sm: 450,
        md: 900,
        lg: Infinity
    }
});

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    render: h => h(App)
});



